<template>
  <div id="building" style="height: 100vh; width: 100%; display: flex; justify-content: center; align-items: center; ">
    <div class="login-box">
      <div style="flex: 1; display: flex; align-items: center; justify-content: center;margin-bottom: 10px;">
        <el-form :model="user" style="width: 80%;" :rules="rules" ref="loginRef">
          <div style="font-size:20px; font-weight: bold; text-align: center; margin-bottom: 20px; margin-top: 30px;">欢迎登录校园安防系统</div>
          <el-form-item prop="username">
            <el-input prefix-icon="el-icon-user" size="medium" placeholder="请输入账号" v-model="user.username"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input prefix-icon="el-icon-lock" size="medium" show-password placeholder="请输入密码" v-model="user.password"></el-input>
          </el-form-item>
          <el-form-item prop="role" style="text-align: left">
            <el-radio v-model="user.role" label="管理员">管理员</el-radio>
            <el-radio v-model="user.role" label="保卫处">保卫处</el-radio>
          </el-form-item>
          <el-form-item prop="code">
            <div style="display: flex">
              <el-input placeholder="请输入验证码" prefix-icon="el-icon-check" size="medium" style="flex: 1" v-model="user.code"></el-input>
              <div style="flex: 1; height: 36px" >
                <valid-code @update:value="getCode"></valid-code>
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 100%; border-radius: 5px" @click="login">登录</el-button>
          </el-form-item>
          <div style="display: flex">
            <div style="flex: 1">还没有账号?请 <span style="color: #409EFF; cursor: pointer" @click="$router.push('/register')">注册</span></div>
            <div style="flex: 1; text-align: right"><span style="color: #409EFF; cursor: pointer" @click = "$router.push('/forgot')">忘记密码</span></div>
            <div></div>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>

<style scoped>
.login-box{
  display: flex;
  background-color: white;
  width: 25%;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  background-color: rgba(255, 255, 255, 0.5);
}

#building{
  background:url("../assets/img.png");
  width:100%;
  height:100%;
  position:fixed;
  background-size:100% 100%;
}
</style>

<script>
import ValidCode from "@/components/ValidCode.vue";
import axios from "axios";

export default {
  name: "Login",
  components: {
    ValidCode
  },
  data() {

    // 验证码校验
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'));
      } else if (value.toLowerCase() !== this.code) {
        callback(new Error('验证码错误'))
      } else {
        callback()
      }
    }
    return {
      code: '',   // 验证码组件传递过来的code
      user: {
        username: '',
        password: '',
        role: '',
        code: ''  // 表单里用户输入的code验证码
      },
      rules: {
        username: [   // 必须和<el-form-item prop="id">里的一样
          { required: true, message: '请输入账号', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
        role: [
          { required: true, message: '请选择身份', trigger: 'change' },
        ],
        code: [
          { validator: validateCode, trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    getCode(code) {
      this.code = code.toLowerCase()      // 将数据变成小写
    },
    login() {
      this.$refs['loginRef'].validate((valid) => {
        if (valid) {
          // 验证通过
          if(this.user.role === "保卫处"){
            axios.post("/api/user/login1",this.user).then(res => {
              if (res.data.code === 1){
                this.$router.push("/security/home")
                this.$message({
                  message: "登录成功",
                  type: "success"
                })
              } else {
                this.$message({
                  message:"登录失败",
                  type:"error"
                })
              }
            })
          } else {
            axios.post("/api/user/login2",this.user).then(res => {
              if (res.data.code === 1){
                this.$router.push("/admin/home")
                this.$message({
                  message: "登录成功",
                  type: "success"
                })
              } else {
                this.$message({
                  message:"登录失败",
                  type:"error"
                })
              }
            })
          }
        }
      })
    }
  }
}
</script>
